.boxlink {
  border: 2px solid black;
   color: black;
  text-transform: uppercase;
  display: inline-block;
  font-size: 16px;
  /*font-weight: bold;*/
  margin: 45px 20px;
  padding: 10px 35px;
  position: relative;
  text-align: center;
  transition: all 0.2s linear 0s;
  width: auto;
  cursor: pointer;
  z-index: 999;
}

.boxlink:hover {
  background: black;
  color: white;
  text-decoration: none;
}

