.cloud {
  z-index: 1;
  opacity: 0.5;
  background-image: url(/assets/img/clouds.jpg);
  width: 600px;
  height: 600px;
  /*background: white;*/
  position: relative;
  /*-webkit-animation: mymove 5s ; /* Safari 4.0 - 8.0 */
  animation: mymove 8s;
  box-shadow: 0 0 80px 80px white inset;
}


/* Standard syntax */

@keyframes mymove {
  0% {
    top: 600px;
  }
  /*25%  {top: 50px;}
    75%  {top: 600px}*/
  100% {
    top: 0px;
  }
}

